/* App.css */

.App {
    text-align: center;
    padding: 20px;
}

.product-list {
    margin-top: 100px;
    /* Menambahkan margin atas sebesar 20px */
}

.products {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.product {
    border: 7px solid #5eec0b;
    border-radius: 8px;
    padding: 10px;
    margin: 10px;
    max-width: 200px;
}

.cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Menengahkan konten vertikal */
    justify-content: center;
    /* Menengahkan konten horizontal */
    height: 100vh;
    /* Membuat div menutupi tinggi layar */
}

.cart-items {
    width: 80%;
    /* Lebar konten item keranjang */
}

.checkout-button {
    margin-top: 20px;
    /* Jarak antara item keranjang dan tombol checkout */
}

.empty-cart-message {
    text-align: center;
    /* Menengahkan teks secara horizontal */
}

.cart-item {
    margin-bottom: 10px;
    border: 1px solid #f70404;
    border-radius: 8px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart-item p {
    margin: 0;
}

.total-price {
    margin-top: 10px;
    font-weight: bold;
}

.product img {
    max-width: 100%;
    /* Gambar akan menyesuaikan lebar container */
    height: auto;
    /* Tinggi gambar menyesuaikan agar proporsi tetap terjaga */
    border-radius: 8px;
    /* Menerapkan border-radius pada gambar */
}


/* App.css */


/* Styling for the cart icon */

.cart-icon {
    position: fixed;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 1000;
    /* Ensure the icon is above other elements */
}

.iconcart {
    font-size: 20px;
    /* Mengatur ukuran font */
    font-family: Arial, sans-serif;
    /* Mengatur jenis font */
    color: black;
    /* Mengatur warna teks */
}


/* Styling for the cart icon image */

.cart-icon img {
    width: 30px;
    /* Adjust the width of the icon */
    height: auto;
    /* Ensure the height adjusts proportionally */
}


/* Styling for the cart count badge */

.cart-count {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 4px;
    font-size: 12px;
}


/* Styling for the Add to Cart button */

.add-to-cart-button {
    background-color: #007bff;
    /* Ubah warna sesuai kebutuhan */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.add-to-cart-button:hover {
    background-color: #0056b3;
    /* Ubah warna sesuai kebutuhan */
}

.icon-with-text {
    display: inline-flex;
    /* Make the icon and text display in a row */
    align-items: center;
    /* Align items vertically */
    margin-left: 30px;
    /* Add some space between the icon and text */
    position: fixed;
}

.link-to-home {
    display: inline-flex;
    /* Make the link and its contents display in a row */
    align-items: center;
    /* Align items vertically */
    text-decoration: none;
    /* Remove underline from the link */
    font-family: Arial, sans-serif;
    /* Change font family */
    font-size: 20px;
    /* Adjust font size as needed */
    color: black;
    /* Change text color */
}

.link-to-home img {
    margin-right: 5px;
    /* Add space between the image and text */
    width: 30px;
    /* Set the width of the image */
    height: 30px;
    /* Set the height of the image */
    position: fixed;
}

.Welcome {
    opacity: 0;
    /* Set opacity to 0 initially */
    animation: fadeIn 3s ease-in-out forwards;
    /* Apply fadeIn animation */
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    /* Center horizontally */
    text-align: center;
    /* Center text horizontally */
    font-size: 24px;
    /* Adjust font size as needed */
    font-family: Arial, sans-serif;
    /* Set font family */
    color: black;
    /* Set text color */
    z-index: 1000;
    /* Set z-index to ensure it appears above other content */
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    /* Start with opacity 0 */
    100% {
        opacity: 1;
    }
    /* End with opacity 1 */
}